<template>
  <section class="blogs" aria-labelledby="blogsHeading" role="section">
    <h2 id="blogsHeading" class="section-header">README.md</h2>
    <div class="blog-flex">
      <Timeline></Timeline>
      <BlogList></BlogList>
    </div>
  </section>
</template>

<script>
import Timeline from './Twitter'
import BlogList from './BlogList'
export default {
  name: 'blogs',
  components: {
    Timeline,
    BlogList
  },
  data () {
    return {
    }
  }
}
</script>

<style scoped lang="scss">
@import '../../variables.scss';
.blogs {
  font: normal normal 14px/1.4 Helvetica,Roboto,"Segoe UI",Calibri,sans-serif;
  background-color: $palette-dark-blue;
  padding: 3em 0 0 0;
}
.blog-flex {
  display: flex;
  min-height: 75vh;
  & > div {
    margin-bottom: 1em;
  }
}
@media (max-width: $mobile-width) {
  .blog-flex {
    flex-direction: column;
  }
}
</style>



